@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../../static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(../../static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
       url(../../static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
       url(../../static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}

.periods-container {
    min-width: 709px;
    margin-top: 18px;
}

button.nav-button {
    padding: 5px 13px 3px;
    border: 1px solid #ECEFF1;
    text-transform: none;
    border-radius: 0;
    margin-right: -1px;
}

button.nav-button:first-child {
    border-radius: 2px 0 0 2px;
}

button.nav-button:last-child {
    border-radius: 0 2px 2px 0;
}

.nav-button.active, .nav-button.active:hover {
    background-color: #00796B;
    color: #fff;
}

.block {
    display: inline-block;
    vertical-align: middle;
}

.block.options {
    width: 50%;
    height: 100%;
    border: 1px solid #e8e8e8;
}

.block.selected-periods {
    width: 38%;
    height: 100%;
    vertical-align: top;
    border: 1px solid #e8e8e8;
}

.block.buttons {
    width: 10%;
    padding: 0 5px;
}

.block.buttons button {
    min-width: 0;
    box-shadow: 0 2px 5px #b1b1b1;
    padding: 7px 10px 5px;
    margin: 0 auto;
    display: block;
}

.block.buttons button:first-child {
    margin-bottom: 35px;
}

.options-area {
    padding: 5px 10px 0px 10px;
    border-bottom: 1px solid #e8e8e8;
}

.periods-list-selected {
    overflow: auto;
    list-style: none;
    height: calc(100% - 81px); /* sum of header and clear button with padding = 81px */
    padding: 0;
    margin: 0px;
}

.sortableHelper {
    z-index: 10000;
}

.periods-list-offered {
    list-style: none;
    overflow: auto;
    height: calc(100% - 93px);
    margin: 0px;
    padding: 0;
}

.period-dimension-item {
    display: -webkit-flex;
    display: flex;
    margin: 2px;
}

.period-offered-label:hover {
    background-color: #e0e0e0;
}

.period-selected-label:hover {
    background-color: #86c4bf;
}

.period-selected-label, .period-offered-label {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 4px;
    padding: 3px 5px 3px 3px;
    outline: none;
    cursor: pointer;
}

.period-selected-label {
    background-color: #b2dfdb;
    padding: 3px;
}

.subtitle-container {
    border-bottom: 1px solid #E0E0E0;
    height: 42px;
}

.selected-periods .subtitle {
    position: relative;
    color: black;
    height: 20px;
    font-size: 15px;
    font-weight: 500;
    top: 12px;
    left: 8px;
}

.selector-area {
    height: 460px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.selector-area .periods-list {
    overflow: auto;
}

.unselected-icon {
    background-color: grey;
    min-height: 6px;
    min-width: 6px;
    margin: 0px 5px;
}

.selected-icon {
    background-color: #00796B;
    min-height: 6px;
    min-width: 6px;
    margin: 0px 5px;
}

.highlighted-icon {
    background-color: white;
    min-height: 6px;
    min-width: 6px;
    margin: 0px 5px;
}

.list-text {
    padding: 2px;
    font-size: 14px;
 }

 .remove-item-button {
    border: none;
    outline: none;
    background: none;
    padding: 0px;
    height: 18px;
    width: 18px;
}

.selector-area .form-control.period-type { width: 65%; padding-right: 10px; }
.selector-area .form-control.year { width: 32%; }
.selector-area .period-li.selected h3 { color: white; }

#year-select {
    cursor: pointer;
    color: #000000;
}

#year-select-menu {
    width: 130px;
}

.react-grid-layout {
  position: relative;
  transition: height 200ms ease;
}
.react-grid-item {
  transition: all 200ms ease;
  transition-property: left, top;
}
.react-grid-item.cssTransforms {
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}
.react-grid-item.resizing {
  z-index: 1;
  will-change: width, height;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  z-index: 3;
  will-change: transform;
}

.react-grid-item.react-grid-placeholder {
  background: red;
  opacity: 0.2;
  transition-duration: 100ms;
  z-index: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.react-grid-item > .react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  cursor: se-resize;
}

.react-grid-item > .react-resizable-handle::after {
  content: "";
  position: absolute;
  right: 3px;
  bottom: 3px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
}

.react-resizable {
  position: relative;
}
.react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url('data:image/svg+xml;base64,');
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}

